body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.flag_icon {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: powderblue;
}
.flag_icon::before {
  content: "";
  background-color: gray;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 8px;
  height: 60px;
}
.flag_icon::after {
  content: "";
  background-color: gray;
  position: absolute;
  top: 50%;
  left: 52%;
  width: 40px;
  height: 30px;
  border-radius: 5px;
  box-shadow: gray;
}